import React from "react"
import {Col,Button,Form} from "react-bootstrap"
import * as contactStyles from "./contact.module.css"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelope, faPhoneAlt, faCheck } from "@fortawesome/free-solid-svg-icons";
import $ from "jquery";
import { Formik } from 'formik';
import * as yup from 'yup';
import { StaticImage } from "gatsby-plugin-image";


export default class Contact extends React.Component {
    constructor(props) {
        super(props);
    }

    goToPage(){
        gtag('event', 'linkedin_link');
    }

    handleSubmit(e){
        const form = e.currentTarget;
        e.preventDefault();
		/*
        console.log($("#formGridName").val())
        console.log($("#formGridEmail").val())
        console.log($("#formGridMessage").val())
		*/
        if (form.checkValidity()) {
            e.stopPropagation();
            $("."+contactStyles.formblock).hide();
            $( "#body_response" ).html("I appreciate you contacting me. I will get back in touch with you soon!");
            $( "."+contactStyles.response_block).show();
        }
        
    }

    render(){
		
        const schema = yup.object().shape({
            name: yup.string().required(),
            email: yup.string().email('Invalid email address').required(),
            message: yup.string().required(),
          });
		 
        return (
            <div className="mainBody">
                <div className={contactStyles.innerBody}>
		            <div className={`${contactStyles.box_contact} ${contactStyles.block1}`}>
			            <div className={contactStyles.response_block}>				            
                            <FontAwesomeIcon icon={faCheck} className={contactStyles.iconFontAwesome}/>					     				            
				            <p id="body_response">Thanks!!</p>
			            </div>
                        <div className={contactStyles.formblock}>
                            <h2>Contact</h2>
                            <p>Questions? Concerns? Comments?</p>
                            <p>Feel free to send me a message and I will get back as soon as possible.</p>
					
                        </div>
		            </div>
                    <div className={`${contactStyles.box_contact} ${contactStyles.block2}`}>
                        <div>
                            <h2>Contact information</h2>
                            
                            <div>
                                <FontAwesomeIcon icon={faPhoneAlt}/> (+44) 07821232546					        	
                            </div>
                        
                            <div>
                                <FontAwesomeIcon icon={faEnvelope}/> lair60@yahoo.es					        	
                            </div>                               
                            
                            <a id="linkedin_link" href="https://uk.linkedin.com/in/lair60" onClick={() => {return this.goToPage('MyLinkedin');}} target="_blank">
                                <StaticImage src="../images/my_linkedin_profile_icon.png" alt="Linkedin Icon" layout="fixed"/>                                
                            </a>
                        </div>
                    </div>
	            </div>
            </div>
        )
    }
}